<template>
  <div>
    <el-dialog
      :title="$t('cardMag.switchingStrategy')"
      :visible.sync="v"
      :before-close="handleClose"
      width="480px"
      :close-on-click-modal="false"
      append-to-body
      @open="open"
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('cardMag.fullMsisdn')">
          <el-input v-model="paramForm.fullMsisdn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.strategyName')">
          <el-input v-model="paramForm.strategyName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.targetStrategy')" prop="newStrategyId">
          <el-select
            v-model="paramForm.newStrategyId"
            :placeholder="selectPh('cardMag.targetStrategy')"
          >
            <el-option
              v-for="item in newStrategyIdOptions"
              :key="item.strategyId"
              :label="item.strategyName"
              :value="item.strategyId"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" :loading="btnLoading" @click="clickOk">{{
          $t('cardMag.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('cardMag.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import langMixin from '@/mixins/langMixin'
export default {
  name: 'ChangePackageDialog',
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '切换策略',
    },
    detailInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {
        fullMsisdn: '',
        strategyName: '',
        newStrategyId: '',
      },
      paramRules: {
        newStrategyId: [{ required: true, message: '请选择目标策略', trigger: 'change' }],
      },
      newStrategyIdOptions: [
        { strategyName: '网络切换策略1', strategyId: '1' },
        { strategyName: '网络切换策略2', strategyId: '2' },
        { strategyName: '网络切换策略3', strategyId: '3' },
      ],
      btnLoading: false,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  methods: {
    open() {
      for (const key in this.paramForm) {
        this.paramForm[key] = this.detailInfo[key]
      }
      this.paramForm.newStrategyId = ''
      this.$nextTick(() => this.$refs.paramFormRef.clearValidate())
      this.queryTargetStrategys()
    },
    queryTargetStrategys() {
      const list = [
        { strategyName: '网络切换策略1', strategyId: '1' },
        { strategyName: '网络切换策略2', strategyId: '2' },
        { strategyName: '网络切换策略3', strategyId: '3' },
      ]
      const strategyId = localStorage.getItem('strategyId') || '1'
      this.newStrategyIdOptions = list.filter((v) => v.strategyId != strategyId)
      // try {
      //   const { custId, sdk, equipmentSupplierId } = this.detailInfo
      //   this.$api.fullStrategyMag
      //     .queryTargetStrategy({
      //       custId,
      //       sdk,
      //       equipmentSupplierId,
      //     })
      //     .then((res) => {
      //       this.newStrategyIdOptions = res?.filter(
      //         (v) => v.strategyId != this.detailInfo.strategyId
      //       )
      //     })
      // } catch (e) {}
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.changeFullSimStrategy()
      })
    },
    changeFullSimStrategy() {
      this.btnLoading = true
      setTimeout(() => {
        localStorage.setItem('strategyId', this.paramForm.newStrategyId)
        this.btnLoading = false
        this.$message.success(`切换策略成功！`)
        this.v = false
        this.$emit('queryFullSimCardListForPage', false)
      }, 1000)

      return
      this.btnLoading = true
      try {
        this.$api.fullCardMag
          .changeFullSimStrategy({
            fullMsisdn: this.paramForm.fullMsisdn,
            newStrategyId: this.paramForm.newStrategyId,
          })
          .then((res) => {
            this.$message.success(`切换策略成功！`)
            this.v = false
            this.$emit('querySimCardListForPage', false)
          })
          .finally(() => {
            this.btnLoading = false
          })
      } catch (e) {
        this.btnLoading = false
      }
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
  },
}
</script>
<style scoped></style>
