<template>
  <div>
    <el-dialog
      :title="$t('cardMag.switchingNetwork')"
      :visible.sync="v"
      :before-close="handleClose"
      width="480px"
      :close-on-click-modal="false"
      append-to-body
      @open="open"
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('cardMag.fullMsisdn')">
          <el-input v-model="paramForm.fullMsisdn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.netIspName')">
          <el-input v-model="paramForm.netIspName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.targetCarrier')" prop="newIspId">
          <el-select
            v-model="paramForm.newIspId"
            :placeholder="selectPh('cardMag.targetCarrier')"
          >
            <el-option
              v-for="item in ispIdOptions"
              :key="item.code"
              :label="item.desc"
              :value="item.code"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" :loading="btnLoading" @click="clickOk">{{
          $t('cardMag.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('cardMag.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import langMixin from '@/mixins/langMixin'
export default {
  name: 'ChangePackageDialog',
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '切网',
    },
    detailInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {
        fullMsisdn: '',
        netIspName: '',
        newIspId: '',
      },
      paramRules: {
        newIspId: [{ required: true, message: '请选择目标运营商', trigger: 'change' }],
      },
      allIspOptions: [
        { desc: this.$t('cardMag.cm'), code: 1 },
        { desc: this.$t('cardMag.cu'), code: 2 },
        { desc: this.$t('cardMag.ct'), code: 3 },
      ],
      ispIdOptions: [
        // { desc: this.$t('cardMag.cm'), code: 1 },
        // { desc: this.$t('cardMag.cu'), code: 2 },
        // { desc: this.$t('cardMag.ct'), code: 3 },
      ],
      btnLoading: false,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  created() {
    // this.queryFullOfferGeneralParam()
  },
  methods: {
    open() {
      for (const key in this.paramForm) {
        this.paramForm[key] = this.detailInfo[key]
        if (this.paramForm.netIspName === '中国移动') {
          this.paramForm.netIspName =
            this.$i18n.locale === 'zh' ? this.paramForm.netIspName : 'China Mobile'
        }
        if (this.paramForm.netIspName === '中国联通') {
          this.paramForm.netIspName =
            this.$i18n.locale === 'zh' ? this.paramForm.netIspName : 'China Unicom'
        }
        if (this.paramForm.netIspName === '中国电信') {
          this.paramForm.netIspName =
            this.$i18n.locale === 'zh' ? this.paramForm.netIspName : 'China Telecom'
        }
      }
      this.paramForm.newIspId = ''
      this.$nextTick(() => this.$refs.paramFormRef.clearValidate())
      this.ispIdOptions =
        this.allIspOptions?.filter((item) => item.code != this.detailInfo.netIspId) || []
    },
    queryFullOfferGeneralParam() {
      try {
        this.$api.fullMealMgr.queryFullOfferGeneralParam().then((res) => {
          this.allIspOptions = res?.Isp
        })
      } catch (e) {}
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.btnLoading = true
        setTimeout(() => this.changeFullNetwork(), 1000)
      })
    },
    changeFullNetwork() {
      this.$api.fullCardMag
        .changeFullNetwork({
          fullMsisdn: this.paramForm.fullMsisdn,
          newIspId: this.paramForm.newIspId,
        })
        .then((res) => {
          this.$message.success(`切网成功！`)
          window.localStorage.setItem('operator', this.paramForm.newIspId)
          const smartCard = { ...this.detailInfo }
          const targetSupplier = this.allIspOptions.find(
            (item) => item.code === this.paramForm.newIspId
          )
          smartCard.netIspId = targetSupplier.code
          smartCard.netIspName = targetSupplier.desc
          this.v = false
          this.$emit('switchNet', smartCard)
        })
        .finally(() => {
          this.btnLoading = false
        })
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
  },
}
</script>
<style scoped></style>
