<script>
import { CHANNEL_ROLE_TYPE } from '@/utils/constant'
import { mapGetters } from 'vuex'
import langMixin from '@/mixins/langMixin'
import ChangeNetDialog from '@/views/infoQuery/cardMag/simDetail/dialogs/ChangeNetDialog.vue'
import ChangeTacticsDialog from '@/views/infoQuery/cardMag/simDetail/dialogs/ChangeTacticsDialog.vue'

const defaultForm = {
  fullMsisdn: '',
  custId: '',
  channelId: '',
  equipmentSupplierId: '',
  fullCardSource: '',
  ispComposeType: '',
  sdk: '',
}
export default {
  components: { ChangeNetDialog, ChangeTacticsDialog },
  mixins: [langMixin],
  data() {
    return {
      labelWidth: '80px',
      channelIdOptions: [],
      links: [{ name: '全能卡管理' }, { name: '号卡管理' }],
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      CHANNEL_ROLE_TYPE,
      tableData: [],
      equipmentSupplierIdOptions: [],
      zhFullCardSourceOptions: [
        { label: '标准卡', value: '1000' },
        { label: '定制卡', value: '2000' },
      ],
      enFullCardSourceOptions: [
        { label: 'Norm card', value: '1000' },
        { label: 'Custom card', value: '2000' },
      ],
      zhIspComposeTypeOptions: [
        { label: '双网', value: '2000' },
        { label: '三网', value: '3000' },
      ],
      enIspComposeTypeOptions: [
        { label: 'Dual networks', value: '2000' },
        { label: 'Three networks', value: '3000' },
      ],
      zhSdkOptions: [
        { label: 'SDK切换', value: '1' },
        { label: '重启切换', value: '2' },
        { label: '断网切换', value: '3' },
      ],
      enSdkOptions: [
        { label: 'SDK switching', value: '1' },
        { label: 'Restart switching', value: '2' },
        { label: 'Off-network switching', value: '3' },
      ],
      zhCustOptions: [
        {
          label: '中国电信',
          value: '1',
          children: [
            { label: '北京电信', value: '1-1' },
            { label: '南京电信', value: '1-2' },
          ],
        },
        {
          label: '中国移动',
          value: '2',
          children: [
            { label: '上海移动', value: '2-1' },
            { label: '武汉移动', value: '2-2' },
            { label: '天津移动', value: '2-3' },
          ],
        },
        {
          label: '中国联通',
          value: '3',
          children: [
            { label: '南昌联通', value: '3-1' },
            { label: '长沙联通', value: '3-2' },
          ],
        },
      ],
      enCustOptions: [
        {
          label: 'China Telecom',
          value: '1',
          children: [
            { label: 'Beijing Telecom', value: '1-1' },
            { label: 'Nanjing Telecom', value: '1-2' },
          ],
        },
        {
          label: 'China Mobile',
          value: '2',
          children: [
            { label: 'Shanghai Mobile', value: '2-1' },
            { label: 'Wuhan Mobile', value: '2-2' },
            { label: 'Tianjin Mobile', value: '2-3' },
          ],
        },
        {
          label: 'China Unicom',
          value: '3',
          children: [
            { label: 'Nanchang Unicom', value: '3-1' },
            { label: 'Changsha Unicom', value: '3-2' },
          ],
        },
      ],
      modifyTitle: '新建',
      modifyParamDialogVisible: false,
      statusCd: '1000',
      selectUserVisible: false,
      selectCustomerVisible: false,
      qryParam: { ...defaultForm },
      saleList: [],
      modifyForm: {},
      detailInfo: {},
      changeNetDialogVisible: false,
      changeTacticsDialogVisible: false,
      custObj: {
        1: 'cm',
        2: 'cu',
        3: 'ct',
      },
    }
  },
  computed: {
    ...mapGetters('user', ['userRoleType']),
    fullCardSourceOptions() {
      return this.$i18n.locale === 'zh'
        ? this.zhFullCardSourceOptions
        : this.enFullCardSourceOptions
    },
    ispComposeTypeOptions() {
      return this.$i18n.locale === 'zh'
        ? this.zhIspComposeTypeOptions
        : this.enIspComposeTypeOptions
    },
    sdkOptions() {
      return this.$i18n.locale === 'zh' ? this.zhSdkOptions : this.enSdkOptions
    },
    custOptions() {
      return [
        { label: '上海农机', value: '1' },
        { label: '贵州人保', value: '2' },
        { label: '南网科技', value: '3' },
      ]
      // return this.$i18n.locale === 'zh' ? this.zhCustOptions : this.enCustOptions
    },
    custName() {
      return (custId) => {
        if (custId == 1) {
          return this.$t('cardMag.cm')
        }
        if (custId == 2) {
          return this.$t('cardMag.cu')
        }
        if (custId == 3) {
          return this.$t('cardMag.ct')
        }
        return '-'
      }
    },
    fullCardSourceDesc() {
      return (fullCardSource) => {
        if (fullCardSource == '1000') {
          return this.$i18n.locale === 'zh' ? '标准卡' : 'Norm card'
        }
        if (fullCardSource == '2000') {
          return this.$i18n.locale === 'zh' ? '定制卡' : 'Custom card'
        }
        return '-'
      }
    },
    ispComposeTypeDesc() {
      return (ispComposeType) => {
        if (ispComposeType == '2000') {
          return this.$i18n.locale === 'zh' ? '双网' : 'Dual networks'
        }
        if (ispComposeType == '3000') {
          return this.$i18n.locale === 'zh' ? '三网' : 'Three networks'
        }
        return '-'
      }
    },
    sdkDesc() {
      return (sdk) => {
        if (sdk == '1') {
          return this.$i18n.locale === 'zh' ? 'SDK切换' : 'SDK switching'
        }
        if (sdk == '2') {
          return this.$i18n.locale === 'zh' ? '重启切换' : 'Restart switching'
        }
        if (sdk == '3') {
          return this.$i18n.locale === 'zh' ? '断网切换' : 'Off-network switching'
        }
        return '-'
      }
    },
    imeiStatusDesc() {
      return (imeiStatus) => {
        if (imeiStatus == '0') {
          return this.$i18n.locale === 'zh' ? '离线' : 'Offline'
        }
        if (imeiStatus == '1') {
          return this.$i18n.locale === 'zh' ? '在线' : 'Online'
        }
        return '-'
      }
    },
    simStatusDesc() {
      return (simStatus) => {
        if (simStatus == '100000') {
          return this.$i18n.locale === 'zh' ? '在用' : 'Active'
        }
        if (simStatus == '130000') {
          return this.$i18n.locale === 'zh' ? '测试激活' : 'Test activation'
        }
        if (simStatus == '140002') {
          return this.$i18n.locale === 'zh' ? '可激活' : 'Can be activated'
        }
        return '-'
      }
    },
  },
  watch: {
    'qryParam.custId': {
      handler(val) {
        this.qryParam.channelId = ''
        this.channelIdOptions = this.custOptions.find((v) => v.value === val).children
      },
      // async handler(val) {
      //   if (!val) return
      //   this.clearChannelIdFn()
      //   const res = await fetch(
      //     '/channel/queryChannelList',
      //     { custId: val },
      //     { method: 'post' }
      //   )
      //   this.channelIdOptions =
      //     res.map((item) => {
      //       return {
      //         value: item.channelId,
      //         label: item.channelName,
      //       }
      //     }) ?? []
      //   if (this.channelIdOptions.length > 0 && this.userRoleType === CHANNEL_ROLE_TYPE) {
      //     this.qryParam.channelId = this.channelIdOptions[0].value
      //   }
      // },
    },
  },
  mounted() {
    this.queryEquipmentSupplierId()
    this.queryFullSimCardListForPage(false)
  },
  methods: {
    queryJTCardCurrentInfo() {
      const operator = window.localStorage.getItem('operator')
      console.log(operator, 'operator')
      if (operator) {
        this.$nextTick(() => {
          if (operator == '1') {
            // 移动
            this.tableData[2].netIspId = 1
            this.tableData[2].netIspName = '中国移动'
          }
          if (operator == '2') {
            // 联通
            this.tableData[2].netIspId = 2
            this.tableData[2].netIspName = '中国联通'
          }
          if (operator == '3') {
            // 电信
            this.tableData[2].netIspId = 3
            this.tableData[2].netIspName = '中国电信'
          }
        })
        return
      }
      this.$api.fullStrategyMag
        .queryJTCardCurrentInfo()
        .then((res) => {
          if (res == 1) {
            // 移动
            this.tableData[2].netIspId = 1
            this.tableData[2].netIspName = '中国移动'
          }
          if (res == 2) {
            // 联通
            this.tableData[2].netIspId = 2
            this.tableData[2].netIspName = '中国联通'
          }
          if (res == 3) {
            // 电信
            this.tableData[2].netIspId = 3
            this.tableData[2].netIspName = '中国电信'
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    queryEquipmentSupplierId() {
      this.$api.fullStrategyMag.queryEquipmentSupplierId().then((res) => {
        this.equipmentSupplierIdOptions = res
      })
    },
    clearChannelIdFn() {
      this.qryParam.channelId = ''
    },
    changeNet(row) {
      this.detailInfo = row
      this.changeNetDialogVisible = true
    },
    rowFormatter(row, col, val) {
      if (row.fullMsisdn == 1810001016) {
        const strategyId = localStorage.getItem('strategyId') || '1'
        return '网络切换策略' + strategyId
      }
      return row.strategyName
    },
    switchNet(newRow) {
      // this.tableData.splice(2, 1, newRow)
      this.queryJTCardCurrentInfo()
    },
    changeTactics(row) {
      this.detailInfo = row
      this.changeTacticsDialogVisible = true
    },
    // 查询业务配置
    async queryFullSimCardListForPage(pageChangeFlag) {
      try {
        this.loading = true
        if (!pageChangeFlag) {
          this.page.pageIndex = 1
          this.page.pageSize = 10
        }
        const params = { ...this.qryParam, ...this.page }
        this.$api.fullCardMag
          .queryFullSimCardListForPage(params)
          .then((res) => {
            this.tableData = res.list || []
            this.page.total = res.total
            this.queryJTCardCurrentInfo()
          })
          .finally(() => {
            this.loading = false
          })
      } catch (e) {
        this.loading = false
      }
    },
    toDetail(row) {
      this.$router.push({
        path: 'cardMagDetail',
        query: {
          fullMsisdn: row.fullMsisdn,
          imeiStatus: row.imeiStatus,
          netIspId: row.netIspId,
        },
      })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.queryFullSimCardListForPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.queryFullSimCardListForPage(true)
    },
    // 重置
    reset() {
      this.qryParam = { ...defaultForm }
      this.queryFullSimCardListForPage(false)
    },
    // 导出
    exportTable() {
      const params = { ...this.qryParam, ...this.page }
      this.$api.fullCardMag.batchExportSimCardList(params).then((response) => {
        const self = this
        const h = this.$createElement
        this.$msgbox({
          title: '操作成功',
          type: 'success',
          center: true,
          message: h('p', { class: 'fs12' }, [
            '文件下载任务已生成，请到下载任务页面',
            h(
              'a',
              { class: 'cor-blue', on: { click: self.viewProgress } },
              '查看任务进度'
            ),
          ]),
          showCancelButton: false,
          confirmButtonText: '知道了',
        })
          .then(() => {})
          .catch(() => {})
      })
    },
    viewProgress() {
      this.$msgbox.close()
      this.$router.push('/userMag/download')
    },
  },
  // destroyed() {
  //   window.localStorage.removeItem('operator')
  //   console.log('destroyed触发')
  // }
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
